import s from "./ProfileInfo.module.css";
import React, {useState} from "react";
import {ProfileType} from "../../../Types/types";
import {Field, Form} from "react-final-form";

type PropsType = {
    isOwner: boolean
    profile: ProfileType | null
    status: string
    updateUserStatus: (status: string) => void
    addChangeAvatar: (file: any) => void
    changeInfoButton: (profile: any) => void
    changeProfile: (profile: any) => void
}

const ProfileDataForm: React.FC<PropsType> = (props) => {
    debugger
    let onSubmit = (value: any) => {
        let profile = {...value, fullName: props.profile && props.profile.fullName, lookingForAJob: isChecked}
        props.changeProfile(profile)
    }

    const [isChecked, setIsChecked] = useState<boolean | null>(props.profile && props.profile.lookingForAJob)

    return (
        <div>

            <Form
                onSubmit={onSubmit}
                render={({handleSubmit}) => (
                    <form action="" onSubmit={handleSubmit}>
                        <button className={s.editButton}>Save profile</button>
                        <button onClick={props.changeInfoButton} className={s.editButton}>Don't save changes</button>
                        <p>About Me:</p>
                        <Field component={"input"} type={'text'} name={'aboutMe'}
                               initialValue={props.profile && props.profile.aboutMe}
                               className={s.form}/>
                        <p> : </p>
                        <span className={s.checkbox} onClick={() => setIsChecked(!isChecked)}>
                             <Field component={"input"} type="checkbox" name={'lookingForAJob'} checked={isChecked}/>
                             <span className={s.wrapper}>
                                  <i className={s.check}></i>
                             </span>
                        </span>
                        <p> : </p>
                        <Field component={"input"} type={'text'}
                               name={'lookingForAJobDescription'}
                               initialValue={props.profile && props.profile.lookingForAJobDescription}
                               className={s.form}/>
                        <p className={s.contacts}></p>
                        <p>  facebook: </p>
                        <Field component={'input'} type={'text'} name={'contacts.facebook'}
                               initialValue={props.profile && props.profile.contacts.facebook}
                               className={s.form}/>
                        <p> -: </p>
                        <Field component={'input'} type={'text'} name={'contacts.website'}
                               initialValue={props.profile && props.profile.contacts.website}
                               className={s.form}/>
                        <p> : </p>
                        <Field component={'input'} type={'text'} name={'contacts.vk'}
                               initialValue={props.profile && props.profile.contacts.vk}
                               className={s.form}/>
                        <p>  twitter: </p>
                        <Field component={'input'} type={'text'} name={'contacts.twitter'}
                               initialValue={props.profile && props.profile.contacts.twitter}
                               className={s.form}/>
                        <p>  instagram:</p>
                        <Field component={'input'} type={'text'} name={'contacts.instagram'}
                               initialValue={props.profile && props.profile.contacts.instagram}
                               className={s.form}/>
                        <p>   youtube: </p>
                        <Field component={'input'} type={'text'} name={'contacts.youtube'}
                               initialValue={props.profile && props.profile.contacts.youtube}
                               className={s.form}/>
                        <p>   github: </p>
                        <Field component={'input'} type={'text'}
                               name={'contacts.github'}
                               initialValue={props.profile && props.profile.contacts.github}
                               className={s.form}/>
                        <p>  mainLink: </p>
                        <Field component={'input'} type={'text'} name={'contacts.mainLink'}
                               initialValue={props.profile && props.profile.contacts.mainLink}
                               className={s.form}/>

                    </form>
                )}
            />
        </div>

    )

};

export default ProfileDataForm;