import {ResultCodesEnum, ResultCodesForCaptchaEnum} from "../api/api";
import {profileAPI} from "../api/profile-api";
import {authAPI} from "../api/auth-api";
import {securityAPI} from "../api/security-api";

const SET_USER_DATA = 'ruslnetwork/authReducer/SET_USER_DATA';
const ADD_USER_PHOTO = 'ruslnetwork/authReducer/ADD_USER_PHOTO';
const ERROR_AUTH = 'ruslnetwork/authReducer/ERROR_AUTH';
const SET_CAPTCHA_URL_SUCCESS = 'ruslnetwork/authReducer/SET_CAPTCHA_URL_SUCCESS';


export type InitialStateType = {
    id: null | number,
    login: null | string,
    email: null | string,
    isFetching: boolean,
    isAuth: boolean,
    userPhoto: null | string,
    errorAuth: null | string,
    captchaURL: null | string
}

let initialState: InitialStateType = {
    id: null,
    login: null,
    email: null,
    isFetching: false,
    isAuth: false,
    userPhoto: '',
    errorAuth: null,
    captchaURL: null
}

const authReducer = (state = initialState, action: any): InitialStateType => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        case ADD_USER_PHOTO:
            return {
                ...state,
                userPhoto: action.userPhoto,
            }
        case ERROR_AUTH:
            return {
                ...state,
                errorAuth: action.message,
            }
        case SET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                captchaURL: action.captchaURL
            }
        default:
            return state;
    }
}


type SetAuthUserDataActionPayloadType = {
    id: number | null
    login: string | null
    email: string | null
    isAuth: boolean
}

type SetAuthUserDataActionType = {
    type: typeof SET_USER_DATA,
    payload: SetAuthUserDataActionPayloadType
}

const setAuthUserData = (id: number | null, login: string | null, email: string | null, isAuth: boolean): SetAuthUserDataActionType => {
    return {
        type: SET_USER_DATA,
        payload: {id, login, email, isAuth}
    }
}

type AddUserPhotoSuccessType = {
    type: typeof ADD_USER_PHOTO
    userPhoto: string
}

const addUserPhotoSuccess = (userPhoto: string): AddUserPhotoSuccessType => {
    return {
        type: ADD_USER_PHOTO,
        userPhoto,
    }
}

export const addUserPhoto = (userId: number) => {
    return async (dispatch: any) => {
        const response = await profileAPI.getUserProfile(userId)
        dispatch(addUserPhotoSuccess(response.data.photos.small))
    }
}

type ErrorAuthType = {
    type: typeof ERROR_AUTH
    message: string | null
}

const errorAuth = (message: string | null): ErrorAuthType => {
    return {
        type: ERROR_AUTH,
        message,
    }
}

type SetCaptchaURLSuccessType = {
    type: typeof SET_CAPTCHA_URL_SUCCESS
    captchaURL: string | null
}

const setCaptchaURLSuccess = (captchaURL: string | null): SetCaptchaURLSuccessType => {
    return {
        type: SET_CAPTCHA_URL_SUCCESS,
        captchaURL
    }
}

const setCaptchaURL = () => {
    return async (dispatch: any) => {
        const response = await securityAPI.captchaURL()
        dispatch(setCaptchaURLSuccess(response.data.url))
    }
}

export const login = (email: any, password: any, rememberMe: any, captcha: any) => {
    return async (dispatch: any) => {
        const data = await authAPI.login(email, password, rememberMe, captcha)
        if (data.resultCode === ResultCodesEnum.Success) {
            dispatch(getAuthUser())
            dispatch(errorAuth(null))
        } else {
            if (data.resultCode === ResultCodesForCaptchaEnum.CaptchaIsRequired) {
                dispatch(setCaptchaURL())
            }
            dispatch(errorAuth(data.messages[0]))
        }
        dispatch(setCaptchaURLSuccess(null))
    }
}

export const logout = () => {
    return async (dispatch: any) => {
        const response = await authAPI.logout()
            if (response.data.resultCode === ResultCodesEnum.Success) {
                dispatch(setAuthUserData(null, null, null, false))
            }
    }
}

export const getAuthUser = () => {
    return async (dispatch: any, getState: any) => {
        const meData = await authAPI.me()
        if (meData.resultCode === 0) {
            dispatch(setAuthUserData(meData.data.id, meData.data.login, meData.data.email, true))
        }
        dispatch(addUserPhoto(getState().auth.id))
    }
}

export default authReducer;
